


































































































































.global-query {
  float: left;
  .queryBox{
    line-height: 50px;
    font-size: 14px;
    overflow: hidden;
    .search-title {
      float: left;
      &:before {
        content: '';
        display: inline-block;
        width: 18px;
        height: 18px;
        background: url("../assets/redLine/sprite.png") 0 -63px no-repeat;
        background-size: auto;
        vertical-align: middle;
        margin-right: 8px;
      }
    }
    .search-title1 {
      float: left;
    }
    .search-item {
      float: left;
      margin-left: 30px;
    }
    .query{
      margin-left: 20px;
      span {
        padding: 5px 10px;
        background: #57a3f3;
        border-radius: 4px;
        color: #fff;
        margin-left: 10px;
      }
      &:first-child {
        margin-left: 0;
      }
    }
  }
  .queryDown{
    position: absolute;
    top: 55px;
    left: 5px;
    right: 5px;
    z-index: 100;
    border: 1px solid #d1d1d1;
    padding: 30px 20px;
    border-radius: 6px;
    width: calc(100% - 10px);
    background: #fff;
    box-shadow: 3px 3px 5px rgba(0,0,0,.2);
    .ivu-form {
      text-align: left;
      overflow: inherit;
      .ivu-form-item {
        margin-bottom: 0;
      }
      .ivu-btn {
        padding: 5px 10px;
        i {
          vertical-align: middle;
          font-size: 13px;
        }
      }
    }
  }
  &#logQuery {
    .queryDown {
      .ivu-form-item {
        .ivu-form-item-label {
          width: 85px !important;
        }
        .ivu-form-item-content {
          margin-left: 85px !important;
          .ivu-select {
            width: 100px !important;
          }
          .ivu-date-picker, ivu-input-wrapper {
            width: 180px !important;
          }
        }
      }
    }
  }
}
